h1, h2, h3, h4, h5, h6, p, ol, ul, span {
  margin: 0;
  padding: 0;
}

body {
  padding: 24px 24px 64px;
}

.wrapper {
  flex-direction: column;
  align-items: center;
  display: flex;
  position: absolute;
  top: 35%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.title {
  letter-spacing: -12px;
  margin-bottom: 24px;
  font-size: 108px;
}

.input_wrapper {
  flex-direction: column;
  align-items: center;
  display: flex;
}

.input {
  width: 600px;
  height: 44px;
  border: 1px solid #000;
  border-radius: 25px;
  padding-left: 24px;
  padding-right: 24px;
  font-size: 20px;
}

.input::-ms-input-placeholder {
  margin-left: 12px;
}

.input::placeholder {
  margin-left: 12px;
}

.btn {
  width: 200px;
  height: 44px;
  background-color: rgba(0, 0, 0, .05);
  border: none;
  border-radius: 12px;
  margin-top: 24px;
  font-size: 20px;
}

.btn:hover {
  border: 1px solid rgba(0, 0, 0, .1);
}

.left_top {
  width: 600px;
  flex-direction: row;
  align-items: baseline;
  gap: 24px;
  top: 5%;
  left: 5.5%;
  transform: translate(0);
}

.min {
  letter-spacing: 0;
  font-size: 36px;
  display: flex;
}

.input_width {
  width: 450px;
}

.btn_hide {
  display: none;
}

.list {
  width: 600px;
  margin-top: 84px;
  margin-left: 36px;
  list-style: none;
}

.item_link {
  color: #000;
  word-break: break-all;
  text-decoration: none;
  overflow: hidden;
}

.item {
  width: 640px;
  margin-bottom: 32px;
}

.item_title {
  color: #1a0dab;
  width: 640px;
  word-break: keep-all;
  margin-top: 8px;
  margin-bottom: 8px;
  font-weight: 400;
}

.item_title:hover {
  cursor: pointer;
  text-decoration: underline;
}

.item_text {
  color: rgba(0, 0, 0, .8);
}

.link_wrapper {
  width: 650px;
}

.g, .little_g {
  color: #4285f4;
}

.first_o {
  color: #ea4335;
}

.second_o {
  color: #fbbc04;
}

.l {
  color: #34a853;
}

.e {
  color: #ea4335;
}

.link_list {
  align-items: center;
  gap: 8px;
  list-style: none;
  display: flex;
}

.link {
  color: #4285f4;
  font-size: 16px;
  text-decoration: none;
}

.link_div {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 4px;
  display: none;
}

.title_bottom {
  letter-spacing: -3px;
  font-size: 36px;
}

.flex {
  display: flex;
}

